import React, { useState } from 'react';
import { visuallyHidden } from '@mui/utils';
import {
    Box,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TablePagination,
    TableRow,
    TableSortLabel,
    Toolbar,
    Typography,
    Paper,
    Checkbox,
    IconButton,
    Tooltip,
    Button,
    InputBase,
    Divider
} from '@mui/material';
import { Action, NavigateNextOutlined } from '@mui/icons-material';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import FilterListIcon from '@mui/icons-material/FilterList';
import { alpha } from '@mui/material/styles';
import CancelPresentationIcon from '@mui/icons-material/CancelPresentation';
import SearchIcon from '@mui/icons-material/Search';
import DirectionsIcon from '@mui/icons-material/Directions';

function descendingComparator(a, b, orderBy) {
    if (b[orderBy] < a[orderBy]) {
        return -1;
    }
    if (b[orderBy] > a[orderBy]) {
        return 1;
    }
    return 0;
}

function getComparator(order, orderBy) {
    return order === 'desc' ? (a, b) => descendingComparator(a, b, orderBy) : (a, b) => -descendingComparator(a, b, orderBy);
}

function stableSort(array, comparator) {
    const stabilizedThis = array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
        const order = comparator(a[0], b[0]);
        if (order !== 0) {
            return order;
        }
        return a[1] - b[1];
    });
    return stabilizedThis.map((el) => el[0]);
}

function EnhancedTableHead(props) {
    const { onSelectAllClick, order, orderBy, numSelected, rowCount, onRequestSort, headCells } = props;

    const [isOpenSearch, setIsOpenSearch] = useState({ id: null, open: false });
    const [wordInSearch, setWordInSearch] = useState({ id: null, isOpen: false });

    const createSortHandler = (property) => (event) => {
        onRequestSort(event, property);
    };

    const handleIsOpenSearch = (id) => {
        setIsOpenSearch({ id, open: true });
    };

    const closeSearchInput = () => setIsOpenSearch({ id: null, open: false });

    console.log(isOpenSearch);

    return (
        <TableHead>
            <TableRow>
                <TableCell padding="checkbox" style={{ width: '50px' }}>
                    <Checkbox
                        color="primary"
                        indeterminate={numSelected > 0 && numSelected < rowCount}
                        checked={rowCount > 0 && numSelected === rowCount}
                        onChange={onSelectAllClick}
                        inputProps={{
                            'aria-label': 'select all desserts'
                        }}
                    />
                </TableCell>
                {headCells.map((headCell) => (
                    <TableCell
                        key={headCell.id}
                        align="center"
                        padding={headCell.disablePadding ? 'none' : 'normal'}
                        sortDirection={orderBy === headCell.id ? order : false}
                    >
                        {isOpenSearch.open && isOpenSearch.id === headCell.id ? (
                            <Paper
                                component="div"
                                align="right"
                                sx={{
                                    zIndex: 1000,
                                    m: '0px 5px',
                                    display: 'flex',
                                    alignItems: 'center',
                                    width: '200',
                                    border: '1px solid #2196F3'
                                }}
                            >
                                <IconButton sx={{ p: '10px' }} aria-label="cancelPresentation" onClick={closeSearchInput}>
                                    <CancelPresentationIcon />
                                </IconButton>
                                <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
                                <InputBase
                                    sx={{ ml: 1, flex: 1 }}
                                    placeholder="Введите текст"
                                    inputProps={{ 'aria-label': 'search google maps' }}
                                />

                                <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
                                <IconButton sx={{ p: '10px' }} aria-label="search">
                                    <SearchIcon />
                                </IconButton>
                            </Paper>
                        ) : (
                            <div>
                                <IconButton
                                    sx={{ p: '10px' }}
                                    style={{ display: orderBy === headCell.id ? '' : 'none' }}
                                    aria-label="search"
                                    onClick={() => handleIsOpenSearch(headCell.id)}
                                >
                                    <SearchIcon />
                                </IconButton>
                                <TableSortLabel
                                    active={orderBy === headCell.id}
                                    direction={orderBy === headCell.id ? order : 'asc'}
                                    onClick={createSortHandler(headCell.id)}
                                >
                                    {headCell.label}
                                    {orderBy === headCell.id ? (
                                        <Box component="span" sx={visuallyHidden}>
                                            {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                                        </Box>
                                    ) : null}
                                </TableSortLabel>
                            </div>
                        )}
                    </TableCell>
                ))}
            </TableRow>
        </TableHead>
    );
}

const EnhancedTableToolbar = (props) => {
    const { numSelected } = props;

    return (
        <Toolbar
            sx={{
                pl: { sm: 2 },
                pr: { xs: 1, sm: 3 },
                pt: { sm: 2 },
                pb: { sm: 2 }
            }}
            style={{ display: 'flex', alignItems: 'flex-start' }}
        >
            <Box
                sx={{
                    width: 'calc(100% - 300px)'
                }}
            >
                <Button variant="contained" startIcon={<FilterListIcon />}>
                    Фильтр
                </Button>
            </Box>
            <Box
                sx={{
                    width: '300px',
                    display: 'flex',
                    alignItems: 'flex-start',
                    justifyContent: 'space-between'
                }}
            >
                <Button variant="contained" startIcon={<EditIcon />} disabled={numSelected < 1 || numSelected > 1}>
                    Изменить
                </Button>
                <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
                <Button variant="contained" startIcon={<DeleteIcon />} disabled={numSelected < 1}>
                    Удалить
                </Button>
            </Box>
        </Toolbar>
    );
};

function EnhancedTable(props) {
    const { headCells, rows, selected, handleSelectAllTableRows, handleSelectTableRow } = props;
    const [order, setOrder] = React.useState('asc');
    const [orderBy, setOrderBy] = React.useState('iin');
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(5);

    const handleRequestSort = (event, property) => {
        const isAsc = orderBy === property && order === 'asc';
        setOrder(isAsc ? 'desc' : 'asc');
        setOrderBy(property);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const isSelected = (id) => selected.indexOf(id) !== -1;

    const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;

    return (
        <Box sx={{ width: '100%' }}>
            <Paper sx={{ width: '100%', mb: 2 }}>
                <EnhancedTableToolbar numSelected={selected.length} />
                <TableContainer>
                    <Table sx={{ minWidth: 750 }} aria-labelledby="tableTitle" size="medium">
                        <EnhancedTableHead
                            numSelected={selected.length}
                            order={order}
                            orderBy={orderBy}
                            onSelectAllClick={handleSelectAllTableRows}
                            onRequestSort={handleRequestSort}
                            rowCount={rows.length}
                            headCells={headCells}
                        />
                        <TableBody>
                            {stableSort(rows, getComparator(order, orderBy))
                                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                .map((row, index) => {
                                    const isItemSelected = isSelected(row.id);
                                    const labelId = `enhanced-table-checkbox-${row.id}`;
                                    return (
                                        <TableRow
                                            hover
                                            onClick={(event) => handleSelectTableRow(event, row.id)}
                                            role="checkbox"
                                            aria-checked={isItemSelected}
                                            tabIndex={-1}
                                            key={row.id}
                                            selected={isItemSelected}
                                        >
                                            <TableCell padding="checkbox" style={{ width: '50px' }}>
                                                <Checkbox
                                                    color="primary"
                                                    checked={isItemSelected}
                                                    inputProps={{
                                                        'aria-labelledby': labelId
                                                    }}
                                                />
                                            </TableCell>
                                            {headCells.map((cell, index) => (
                                                <TableCell align="center" key={cell.id}>
                                                    {row[cell.id]}
                                                </TableCell>
                                            ))}
                                        </TableRow>
                                    );
                                })}
                            {emptyRows > 0 && (
                                <TableRow
                                    style={{
                                        height: 53 * emptyRows
                                    }}
                                >
                                    <TableCell colSpan={6} />
                                </TableRow>
                            )}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[5, 10, 25]}
                    component="div"
                    count={rows.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onPageChange={handleChangePage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                />
            </Paper>
        </Box>
    );
}

export default EnhancedTable;
